









import Vue from 'vue';
export default Vue.extend({
  name: 'loadmoreCard',
  props: {
    data: {
      type: Object,
      required: true,
    },
    config: {
      type: Object,
    },
  },
  data() {
      return{
          showCMNTName : '',
      };
  },
  created() {
    // console.log("create");
    this.showCMNTName = this.config.showName || '评论';
  },
  methods: {
    doSecLoadmore(): void {
      this.$emit('do-sec-loadmore', { data: this.data });
    },
  },
});
